import {Box, Button, Stack, Typography, useTheme} from "@mui/material";
import React from "react";
import {Spacer, useMobile} from "./Extensions";

interface PanelContentProps {
  flavor: string,
  title: string,
  description: React.ReactNode,
  align: "right" | "left",
  primaryButtonTitle?: string,
  primaryButtonUrl?: string,
  secondaryButtonTitle?: string,
  secondaryButtonUrl?: string,
}

/** A centered box with optional background that keeps content an expected width */
export const PanelContent: React.FC<PanelContentProps> = ({align, flavor, title, description, primaryButtonTitle, primaryButtonUrl, secondaryButtonTitle, secondaryButtonUrl}) => {
  const theme = useTheme();
  const isMobile = useMobile();
  return (
    <Box sx={{display: "flex", justifyContent: align, maxWidth: 1024, width: 1024}}>
      <Stack sx={{
        textShadow: "0 0 1px transparent,0 2px 3px rgba(0,0,0,.8)",
        maxWidth: 768,
      }} spacing={1}>
        <Typography align={isMobile ? "center" : align} variant="h5" textTransform="uppercase">{flavor}</Typography>
        <Typography align={isMobile ? "center" : align} variant={isMobile ? "h3" : "h2"} textTransform="uppercase" fontWeight="bold">{title}</Typography>
        <Typography align={isMobile ? "center" : align} variant="h6">{description}</Typography>
        <Spacer size={1} />
        <Stack spacing={1} direction="row" justifyContent={isMobile ? "center" : align}>
          {primaryButtonTitle && <Button variant="contained" size="large">{primaryButtonTitle}</Button>}
          {secondaryButtonTitle && <Button variant="contained" size="large">{secondaryButtonTitle}</Button>}
        </Stack>
      </Stack>
    </Box>
  );
};
